.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

.row {
  margin: -1rem 0 1rem 0;
}

html, body {
  font-family: "iransans-per-num", iransans, Helvetica, Geneva, Verdana, sans-serif;
  text-align: right;
  margin: 0;
  direction: rtl;
  line-height: 1.6;
}

a, a:hover, a:visited, a:active {
  text-decoration: none;
}

a:hover, a:active {
  color: #00b8d4;
}

.english-num {
  font-family: "iransans", Helvetica, Geneva, Verdana, sans-serif;
}

span.bar {
  width: 65px;
  height: 3px;
  background: #00b8d4;
  display: block;
  position: relative;
  top: 19px;
  right: -5px;
}

.margin-top-20 {
  margin-top: 20px;
}

.margin-top-40 {
  margin-top: 40px;
}

.black-back-slider {
  background: #000;
  overflow: hidden;
  margin: 0 auto;
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  z-index: -100;
  transition: all 1s ease-in-out;
}

#bingo-intro-slider .slider {
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../img/img1.jpg");
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
  margin: 0 auto;
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  display: none;
  z-index: -100;
  -webkit-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
}

#bingo-intro-slider .slider.active {
  display: block;
  animation: fading 0.5s;
}

.slider-selector ul li {
  float: left;
  margin-right: 7px;
  list-style: none;
  width: 10px;
  height: 10px;
  background: #fff;
  border-radius: 100%;
  cursor: pointer;
}

.slider-selector ul li:hover, .slider-selector ul li.active {
  background: #00b8d4;
}

header {
  color: #00b8d4;
}

header a, header a:hover, header a:visited, header a:active {
  color: #9e9e9e;
}

header nav .menu {
  float: right;
}

header nav .menu ul {
  margin-top: 25px;
  margin-right: 25px;
  list-style: none;
}

header nav .menu ul li {
  float: right;
  padding: 0 15px;
  cursor: pointer;
  -webkit-transition: border-color .5s ease-in-out;
  transition: border-color .5s ease-in-out;
  border-top: 2px solid transparent;
}

header nav .menu ul li:hover {
  border-bottom: 2px solid #00b8d4;
}

header nav .logo {
  float: left;
  width: 240px;
  margin-top: 25px;
  margin-left: 50px;
}

header nav .logo .sign-up-btn {
  border-left: 1px solid #fff;
  display: inline-block;
  padding-left: 25px;
}

header nav .logo .sign-up-btn a {
  border: 1px solid #FFFFFF;
  border-radius: 20px;
  padding: 2px 15px;
  -moz-transition: all .2s ease-in;
  -o-transition: all .2s ease-in;
  -webkit-transition: all .2s ease-in;
  transition: all .2s ease-in;
}

header nav .logo .sign-up-btn a:hover {
  background-color: #00b8d4;
}

header nav .logo .bingo {
  float: left;
  font-weight: bolder;
  margin-top: -5px;
  margin-right: 10px;
  font-size: 25px;
}

header.standard nav {
  display: inline-block;
  width: 100%;
  margin-top: 20px;
}

header.intro {
  width: 100%;
  height: 100vh;
  color: #FFFFFF;
}

header.intro a, header.intro a:hover, header.intro a:visited, header.intro a:active {
  color: #FFFFFF;
}

header.intro .slider-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 70%;
}

header.intro .slider-content span.bar {
  width: 65px;
  height: 3px;
  top: 35px;
  right: -5px;
}

header.intro .slider-content h4 {
  font-size: 30px;
}

header.intro .slider-content p {
  font-size: 20px;
}

header.intro .footer {
  position: absolute;
  width: 100%;
  bottom: 60px;
}

header.intro .footer .social {
  float: right;
  padding-top: 5px;
  margin-right: 10%;
}

header.intro .footer .social span {
  padding: 10px;
  padding-left: 0;
  color: #d5d5d5;
  font-size: 20px;
}

header.intro .footer .social span a, header.intro .footer .social span a:visited {
  color: #d5d5d5;
}

header.intro .footer .social span a:hover, header.intro .footer .social span a:active {
  color: #00b8d4;
}

header.intro .footer .arrow-key {
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
  display: inline-block;
  width: 40px;
  height: 30px;
  background: #00b8d4;
  color: #fff;
  padding-top: 10px;
  border-radius: 50%;
  text-align: center;
  cursor: pointer;
}

header.intro .footer .arrow-key .fa {
  -webkit-animation: downUp 0.45s;
  animation: downUp 0.45s;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}

header.intro .footer .slider-selector {
  float: left;
  padding-top: 5px;
  margin-left: 10%;
}

header.intro .footer .slider-selector ul {
  margin-top: 12px;
}

footer {
  width: 85%;
  margin: 45px auto 100px;
  color: #8a8a8a;
  font-size: 14px;
}

footer a, footer a:visited {
  color: #8a8a8a;
}

footer a:hover, footer a:active {
  color: #00b8d4;
}

footer p, footer ul {
  margin: 0;
  list-style: none;
  padding: 5px;
}

footer h4 {
  color: #000;
  font-weight: bold;
  font-size: 20px;
  margin-bottom: 10px;
}

footer .colorful-text {
  color: #00b8d4;
}

footer h4.colorful-text {
  font-size: 28px;
  margin-top: 19px;
  margin-bottom: 5px;
}

footer .mobile {
  float: right;
}

footer .email {
  float: left;
  color: #00b8d4;
}

footer .social {
  clear: both;
  float: left;
  border-top: 1.5px solid #d5d5d5;
  padding-top: 5px;
}

footer .social span {
  padding: 10px;
  padding-left: 0;
  color: #d5d5d5;
  font-size: 20px;
}

footer .social span a, footer .social span a:visited {
  color: #d5d5d5;
}

footer .social span a:hover, footer .social span a:active {
  color: #00b8d4;
}

img {
  width: 100%;
}

section {
  max-width: 970px;
  margin: 0 auto;
}

section .title h4 {
  margin-bottom: -30px;
}

section h3 {
  text-align: center;
  font-size: 35px;
  font-weight: bold;
}

section h3 + span.bar {
  width: 65px;
  height: 3px;
  background: #00b8d4;
  display: block;
  margin: -35px auto 0;
  position: relative;
}

section .products {
  text-align: center;
}

section .products .product-image {
  width: 70%;
  text-align: center;
  margin: 0 auto;
}

section .products.slide {
  text-align: center;
}

section .products.slide .product-image {
  width: 100%;
  text-align: center;
  margin: 0 auto;
}

section .products.slide .carousel-wrapper {
  width: 100%;
  height: 300px;
  position: -webkit-sticky;
  position: sticky;
  top: 50%;
  margin: 0 -10%;
  overflow: hidden;
  display: inline-table;
  overflow-y: visible;
  margin: 0 auto;
}

section .products.slide .carousel-wrapper .carousel {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  height: auto;
}

section .products.slide .carousel-wrapper .carousel .carousel-cell {
  padding: 10px;
  background-color: #FFFFFF;
  width: 20%;
  height: auto;
  min-width: 120px;
  margin: 0 20px;
  transition: transform 500ms ease;
}

section .products.slide .carousel-wrapper .carousel .carousel-cell .more {
  display: block;
  opacity: 0;
  margin: 5px 0 15px 0;
  text-align: center;
  font-size: 10px;
  color: #CFCFCF;
  text-decoration: none;
  transition: opacity 300ms ease;
}

section .products.slide .carousel-wrapper .carousel .carousel-cell .more:hover, section .products.slide .carousel-wrapper .carousel .carousel-cell .more:active, section .products.slide .carousel-wrapper .carousel .carousel-cell .more:visited, section .products.slide .carousel-wrapper .carousel .carousel-cell .more:focus {
  color: #CFCFCF;
